body {
    font-family: 'Arial', sans-serif;
    background-color: #FAF8EF;
  }
  
  .header {
    background-color: #A6674C;
    padding: 20px;
    text-align: center;
  }
  
  .header h1 {
    color: white;
    font-size: 2em;
  }
  
  .search-results {
    padding: 20px;
  }
  
  .search-results h2 {
    color: #A6674C;
  }
  
  .search-results-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
  
  .search-result-item {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    text-align: center;
  }
  
  .search-result-item h3 {
    color: #A6674C;
  }
  
  .search-result-item p {
    color: #333;
  }
  