body {
    font-family: 'Arial', sans-serif;
    background-color: #FAF8EF;
  }
  
  .header {
    background-color: #A6674C;
    padding: 20px;
    text-align: center;
  }
  
  .header h1 {
    color: white;
    font-size: 2em;
  }
  
  .product-detail {
    display: flex;
    justify-content: center;
    padding: 20px;
  }
  
  .product-info {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    width: 60%;
  }
  
  .product-info h2 {
    color: #A6674C;
  }
  
  .product-info p {
    color: #333;
  }
  
  .product-stats {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .product-stats div {
    background-color: #f9f9f9;
    padding: 10px;
    border-radius: 5px;
    width: 45%;
    text-align: center;
  }
  