body {
    font-family: 'Arial', sans-serif;
    background-color: #FAF8EF;
  }
  
  .header {
    background-color: #A6674C;
    padding: 20px;
    text-align: center;
  }
  
  .header h1 {
    color: white;
    font-size: 2em;
  }
  
  .search-bar {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .search-bar input {
    width: 50%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px 0 0 5px;
  }
  
  .search-bar button {
    padding: 10px;
    background-color: #A6674C;
    color: white;
    border: none;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
  }
  
  .search-bar button:hover {
    background-color: #8b5337;
  }
  
  .think-big {
    text-align: center;
    font-size: 1.5em;
    color: #A6674C;
  }
  