body {
    font-family: 'Arial', sans-serif;
    background-color: #FAF8EF;
  }
  
  .header {
    background-color: #A6674C;
    padding: 20px;
    text-align: center;
  }
  
  .header h1 {
    color: white;
    font-size: 2em;
  }
  
  .recommendation {
    padding: 20px;
  }
  
  .recommendation h2 {
    color: #A6674C;
    text-align: center;
  }
  
  .recommendation-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .recommendation-item {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    width: 30%;
    text-align: center;
  }
  
  .recommendation-item h3 {
    color: #A6674C;
  }
  
  .recommendation-item p {
    color: #333;
  }
  
  .recommendation-details {
    margin-top: 20px;
    padding: 20px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 10px;
  }
  